@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
/* @import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap'); */

* {
	box-sizing: border-box;
}

html {
	/* font-family: 'Open Sans', sans-serif; */
	font-family: 'Muli', sans-serif;
}

body {
	margin: 0;
	background: #4d4d4d;
	color: aliceblue;
}

.slide-in-left-enter {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}

.slide-in-left-enter-active {
	-webkit-transform: translateX(0);
	        transform: translateX(0);
	opacity: 1;
	transition: all 200ms;
}

.slide-in-left-exit {
	-webkit-transform: translateX(0%);
	        transform: translateX(0%);
	opacity: 1;
}

.slide-in-left-exit-active {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
	opacity: 0;
	transition: all 200ms;
}

.center {
	text-align: center;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.container {
	width: 80%;
	margin: 0 auto;
}

.mb-0 {
	margin-bottom: 0;
}

.mb-1 {
	margin-bottom: 0.25rem;
}

.mb-2 {
	margin-bottom: 0.5rem;
}

.mb-3 {
	margin-bottom: 1rem;
}

.button {
	font: inherit;
	padding: 0.5rem 1.5rem;
	border: 1px solid #f8df00;
	border-radius: 4px;
	background: #f8df00;
	color: #222;
	cursor: pointer;
	margin-right: 1rem;
	text-decoration: none;
	display: inline-block;
}

.button:focus {
	outline: none;
}

.button:hover,
.button:active {
	background: #d8c200;
	border-color: #d8c200;
}

.button--inverse {
	background: transparent;
	color: #f8df00;
}

.button--inverse:hover,
.button--inverse:active {
	color: #222;
	background: #f8df00;
}

.button--danger {
	background: #de0000;
	border-color: #de0000;
	color: #fff;
}

.button--danger:hover,
.button--danger:active {
	background: #bf0000;
	border-color: #bf0000;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
	background: #ccc;
	color: #979797;
	border-color: #ccc;
	cursor: not-allowed;
}

.button--small {
	font-size: 0.8rem;
}

.button--big {
	font-size: 1.5rem;
}

.form-control {
	margin: 1rem 0;
}

.form-control label,
.form-control input,
.form-control textarea {
	display: block;
}

.form-control label {
	font-weight: bold;
	margin-bottom: 0.5rem;
}

.form-control input,
.form-control textarea {
	width: 100%;
	font: inherit;
	border: 0 solid #4d4d4d;
	background: #4d4d4d;
	padding: 0.5rem;
	border-radius: 0.25rem;
	color: #ccc;
	transition: box-shadow 0.15s ease;
}

.form-control input:focus,
.form-control textarea:focus {
	outline: none;
	background: #4d4d4d;
	/* border-color: #897b00; */
	box-shadow: inset 0px 0px 5px 1px #0008;
}

/* .form-control--invalid label, */
.form-control--invalid p {
	color: #d00;
}

.form-control--invalid input,
.form-control--invalid textarea {
	/* border-color: red;
	background: #ffd1d1; */
	box-shadow: inset 0px 0px 5px 0 #f008;
}

.place-form {
	position: relative;
	list-style: none;
	margin: 0 auto;
	padding: 1rem;
	width: 90%;
	max-width: 40rem;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
	border-radius: 6px;
	background: #292929;
}

.card {
	position: relative;
	margin: 0;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
	border-radius: 6px;
	padding: 1rem;
	overflow: hidden;
	background: #292929;
	transition: all 0.5s ease;
}

.map {
	width: 100%;
	height: 100%;
}

.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.75);
	z-index: 10;
	transition: background-color 0.5s ease;
}

.modal {
	z-index: 100;
	position: fixed;
	top: 22vh;
	left: 10%;
	width: 80%;
	background: #4d4d4d;
	color: #ccc;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
	border-radius: 1rem;
}

.modal__header {
	width: 100%;
	padding: 1rem 0.5rem;
	background: #292929;
	border-radius: 1rem 1rem 0 0;
}

.modal__header h2 {
	margin: 0.5rem;
}

.modal__content {
	padding: 1rem 0.5rem;
}

.modal__footer {
	padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
	.modal {
		left: calc(50% - 17.5rem);
		width: 35rem;
	}
}

.modal-enter {
	-webkit-transform: translateY(-10rem);
	        transform: translateY(-10rem);
	opacity: 0;
}

.modal-enter-active {
	-webkit-transform: translateY(0);
	        transform: translateY(0);
	opacity: 1;
	transition: all 200ms;
}

.modal-exit {
	-webkit-transform: translateY(0);
	        transform: translateY(0);
	opacity: 1;
}

.modal-exit-active {
	-webkit-transform: translateY(-10rem);
	        transform: translateY(-10rem);
	opacity: 0;
	transition: all 200ms;
}

.place-item {
	margin: 1rem 0;
}

.place-item__content {
	padding: 0;
}

.place-item__info {
	padding: 1rem;
	text-align: center;
}

.place-item__image {
	width: 100%;
	height: 12.5rem;
	margin-right: 1.5rem;
}

.place-item__image img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.place-item__info h2,
.place-item__info h3,
.place-item__info p {
	margin: 0 0 0.5rem 0;
}

.place-item__actions {
	padding: 1rem;
	text-align: center;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	border-top: 1px solid #4d4d4d;
}

.place-item__actions-restricted {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.place-item__actions-restricted > a,
.place-item__actions-restricted > button {
	width: 90%;
}

.place-item__modal-content {
	padding: 0;
}

.place-item__modal-actions {
	text-align: right;
}

.place-item__actions button,
.place-item__actions a {
	margin: 0.5rem;
}

.map-container {
	height: 15rem;
	width: 100%;
}

@media (min-width: 768px) {
	.place-item__image {
		height: 20rem;
	}
	.place-item__actions {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: row;
		        flex-direction: row;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
	}
	.place-item__actions.not-logged-in {
		-webkit-justify-content: center;
		        justify-content: center;
	}
	.place-item__actions-restricted {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: row;
		        flex-direction: row;
	}
}

.place-list {
  list-style: none;
  margin: 1rem auto;
  padding: 0;
  width: 90%;
  max-width: 40rem;
}

.main-header {
	width: 100%;
	height: 4rem;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	position: fixed;
	top: 0;
	left: 0;
	background: #292929;
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
	padding: 0 1rem;
	z-index: 5;
}

main {
	margin-top: 5rem;
}

@media (min-width: 768px) {
	.main-header {
		-webkit-justify-content: space-between;
		        justify-content: space-between;
	}
}

.nav-links {
	list-style: none;
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.nav-links li {
	margin: 1rem;
}

.nav-links a {
	/* border: 1px solid transparent; */
	color: #ccc;
	text-decoration: none;
	padding: 0.5rem;
	border-radius: 0.5rem;
	transition: background-color 0.5s ease, color 0.5s ease;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
	background-color: #f8df00;
	border-color: #292929;
	color: #292929;
}

.nav-links button {
	cursor: pointer;
	border: none;
	border-radius: 0.5rem;
	color: #ccc;
	background: transparent;
	padding: 0.5rem;
	font: inherit;
	transition: background-color 0.5s ease, color 0.5s ease;
}

.nav-links button:focus {
	outline: none;
}

.nav-links button:hover,
.nav-links button:active {
	background: #de0000;
	color: #fff;
}

@media (min-width: 768px) {
	.nav-links {
		-webkit-flex-direction: row;
		        flex-direction: row;
	}

	.nav-links li {
		margin: 0 0.5rem;
	}

	.nav-links a {
		color: #ccc;
		text-decoration: none;
	}

	.nav-links button {
		color: #ccc;
		background: transparent;
	}

	.nav-links button:hover,
	.nav-links button:active {
		background: #de0000;
		color: #fff;
	}
}

.side-drawer {
	position: fixed;
	left: 0;
	top: 0;
	z-index: 100;
	height: 100vh;
	width: 70%;
	background: #222e;
	color: #ccc;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.main-navigation__menu-btn {
	width: 3rem;
	height: 3rem;
	background: transparent;
	border: none;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	margin-right: 2rem;
	cursor: pointer;
}

.main-navigation__menu-btn span {
	display: block;
	width: 100%;
	padding: 0;
	height: 10%;
	background: #f8df00;
	border: none;
	border-radius: 1rem;
}

.main-navigation__menu-btn span:first-child {
	width: 33.3%;
}
.main-navigation__menu-btn span:last-child {
	width: 66.66%;
}

.main-navigation__title {
	color: #f8df00;
	/* color: white; */
}

.main-navigation__title a {
	text-decoration: none;
	color: #f8df00;
	/* color: white; */
}

.main-navigation__header-nav {
	display: none;
}

.main-navigation__drawer-nav {
	height: 100%;
}

@media (min-width: 768px) {
	.main-navigation__menu-btn {
		display: none;
	}

	.main-navigation__header-nav {
		display: block;
	}
}

.authentication__header {
	color: white;
	text-align: center;
}

.authentication {
	width: 90%;
	max-width: 25rem;
	margin: 7rem auto;
	text-align: center;
}

.authentication form {
	margin-bottom: 1rem;
}

.switch-login-signup {
	margin-top: 3rem;
}

.switch-login-signup button {
	width: 100%;
}

.authentication hr {
	border: none;
	box-shadow: 0 0 1px 0.2px #f8df00;
}

.user-item {
	margin: 1rem;
	width: calc(45% - 2rem);
	min-width: 17.5rem;
}

.user-item a {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
	height: 100%;
	text-decoration: none;
	padding: 1rem;
	color: white;
	background: #292929;
	transition: all 0.5s ease;
}

.user-item a:hover,
.user-item a:active {
	background: #ffd900;
}

.user-item__content {
	padding: 0;
	transition: all 0.5s ease;
}

.user-item__image {
	width: 4rem;
	height: 4rem;
	margin-right: 1rem;
}

.user-item__info h2 {
	font-size: 1.5rem;
	margin: 0 0 0.5rem 0;
	font-weight: normal;
	color: #ffd900;
	transition: all 0.5s ease;
}

.user-item:hover h2,
.user-item:active h2,
.user-item:hover h3,
.user-item:active h3 {
	color: #292929;
}

.user-item__info h3 {
	margin: 0;
	transition: all 0.5s ease;
}

.avatar {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex; 
  -webkit-justify-content: center; 
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.avatar img {
  display: block;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.users-list {
	list-style: none;
	margin: 0 auto;
	padding: 0;
	width: 90%;
	max-width: 50rem;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.no-users {
	color: #252525;
	padding: 0 1.5rem;
}

